.project-intro {
    display: flex;
    justify-content: left;
    align-items: center;
    gap: 10px;
    flex-wrap: wrap;
    flex-direction: row;

    .project-img {
        max-width: 250px;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 10px;
        flex-wrap: wrap;
        flex-direction: column;
    }

    .project-vertical-hr {
        align-self: stretch;
        margin: 0 20px;
        width: 0px;
    }

    .project-content {
        flex: 1;

        h1 {
            font-size: 40px;
        }
    }
}

@media (max-width: 1200px) {
    .project-content {
        .title-with-element {
            .element {
                float: none;
                margin: 20px auto;
            }
            h1 {
                text-align: center;
                display: block;
            }
        }
    }
}

@media (max-width: 1000px) {
    .project-intro {
        display: block;

        .project-img {
            max-width: 100%;
            margin: 20px auto;
        }
        .project-vertical-hr {
            display: none;
        }
    }
}